teY(0)}}@media screen and (min-width: 1024px){.search{height:88vh}.search__play{margin-top:13vh;width:40vw;height:30vh}.search__form{margin-top:10vh;width:40%;height:6vh}.search__form--text{width:75%;height:4.5vh;padding:min(3vh, 2rem) max(1vw, 1rem);font-size:max(0.9vw, 2.0rem)}.search__popcorn{margin-left:calc(-55vw / 2);width:55vw;height:30vh;animation-name:popCornTab}}@keyframes popCornTab{0%{transform:translateY(30vh)}100%{transform:translateY(0)}}@keyframes transPage{0%{transform:translateX(-100%)}100%{transform:translateX(0)}}.results-loading{width:100%;min-height:91vh;display:flex;justify-content:center;align-items:center}.results-loading__spinner{width:8rem;height:8rem;border:.8rem solid transparent;border-radius:50%;border-top-color:#f6c700;animation:loadingSpinner 1000ms linear infinite}@media screen and (min-width: 1024px){.results-loading{min-height:88vh}}@keyframes loadingSpinner{to{transform:rotate(1turn)}}.results{width:100%;padding:0 9vw 0 9vw;min-height:91vh;display:flex;flex-direction:column;align-items:center;animation:transPageInverse 300ms ease-in}.results__poster{margin-top:10vh;width:60vw;height:47vh}.results__title{margin-top:6vh;font-family:"Merriweather",serif;font-size:7.5vw;text-align:center}.results__summary{margin-top:4vh;font-family:"Nunito",sans-serif;font-size:5.2vw;text-align:center}.results__dir{margin-top:2vh;font-family:"Nunito",sans-serif;font-size:4vw;text-align:center}.results__cast{margin-top:2vh;font-family:"Nunito",sans-serif;font-size:3.5vw;text-align:center}.results__btn{margin:2.5vh 0}@media screen and (min-width: 1024px){.results{width:100vw;padding:6vh 8vw 6vh 8vw;height:88vh;max-height:88vh;min-height:0;display:grid;grid-template-columns:repeat(2, 1fr);grid-template-rows:41vh 25vh 10vh;justify-content:space-around;grid-template-areas:"poster title" "poster info" "btn ."}.results__poster{grid-area:poster;justify-self:center;align-self:center;margin:0 auto;width:70%;height:90%}.results__title{grid-area:title;align-self